import React from 'react'
import TableMain from 'pages/admin/assets/styles/components/TableMain';
import AdminServices from 'services/api/AdminServices';
import csvDownload from 'json-to-csv-export'

export default function ManageClients(props) {
  const [data, setData] = React.useState({})

  const actions = ['View details']

  React.useEffect(() => {
    const getIFAClients = async (id) => {
      let res = await AdminServices.getIFAClients(id).then(res => res);
      if (res.type === true) {
        const aa = res.data;
        setData(aa)
        console.log("Get all clients API :", aa)
      }
    }
    getIFAClients(-1);
  }, [])

  const actionClicked = (index, rowData) => {
    switch (index) {
      case 0:
        props.history.push("/admin/clients/details/" + rowData.ClientId)
        break;
    }
  }


  return (
    <div>
      <div className="page-header">
        <h3 className="page-title"> Your clients </h3>
        <button type="button" className="btn btn-primary btn-fw" onClick={() => csvDownload(data)}>
          Export to csv <span className="mdi mdi-file-excel"></span>
        </button>
        {/* <nav aria-label="breadcrumb"> 
          <ol className="breadcrumb">
            <li className="breadcrumb-item"><a href="!#" onClick={event => event.preventDefault()}>Tables</a></li>
            <li className="breadcrumb-item active" aria-current="page">Basic tables</li>
          </ol>
        </nav> */}
      </div>
      <div className="grid-margin stretch-card">
        <div className="card">
          <div className="card-body">
            {/* <h4 className="card-title"></h4> */}
            <TableMain
              navigation={props.history}
              data={data}
              others={actions}
              onActionClick={(i, r) => actionClicked(i, r)}
              tableSearchPlaceholder="Search by client name"
              searchQueryVar={"DisplayName"}
              notRequired={[
                "Password",
                "OldPassword",
                "IsProfileComplete",
                "IsMFDEnabled",
                "IsKYCCompliant",
                "IsBSEComplete",
                "IsBlackPayPending",
                "IsMandateRegisterd",
                "PIN",
                "IsPasswordTemp",
                "PageNumber",
                "LastLogin",
                "LoginAttempts",
                "IsAccountLocked",
                "Is2FAEnabled",
                "UpdatedOn"
              ]}
              header="All clients"
            />
          </div>
        </div>
      </div>
    </div>
  )
}